footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: hsla(var(--black), 1);
  box-shadow: 0px 0px 10px 0px hsla(var(--black), 0.5);
}

footer .external__navigation ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}

footer .external__navigation ul a {
  text-decoration: none;
  color: hsla(var(--white), 1);
  font-size: 1.5rem;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  transition: all 0.3s ease;
}
footer .external__navigation ul a:hover {
  text-decoration: underline;
  color: hsla(var(--gold), 1);
}

@media screen and (max-width: 768px) {
  footer {
    flex-direction: column;
    gap: 1rem;
  }

  footer .external__navigation ul a {
    font-size: 0.8rem;
  }
}
